import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const StyledButton = withStyles({
    root: {
        background: '#0c539a',
        borderRadius: 5,
        border: 0,
        color: 'white',
        padding: '12px 30px',
    },
    label: {
        textTransform: 'capitalize',
    },
})(Button);

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(1),
    },
}))(MuiDialogActions);


export default function MyModal(props) {
    let {children, label, saveBtnLabel, button, hideBtn, buttonTitle, contentStyle, onSave, onClose} = props;
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <div onClick={handleClickOpen}>
                {
                    button
                        ? button
                        : <StyledButton variant="contained">{buttonTitle}</StyledButton>
                }
            </div>

            <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
                <DialogTitle id="customized-dialog-title" onClose={() => {handleClose(); onClose()}}>
                    {label}
                </DialogTitle>
                <DialogContent dividers style={contentStyle} >
                    {children}
                </DialogContent>

                {
                    !hideBtn &&
                    <DialogActions>
                        <Button autoFocus
                                variant={"contained"}
                                size={'medium'}
                                style={{color: '#fff', backgroundColor: '#00c6ab'}}
                                className='col py-2 m-2'
                                onClick={() => {onSave(); handleClose(); onClose()}}
                        >
                            {saveBtnLabel || 'Save changes'}
                        </Button>
                    </DialogActions>
                }
            </Dialog>
        </div>
    );
}
